import type { HttpError } from '@wix/fe-essentials/http-client';
import {
  ErrorResponseDetails,
  ErrorResponseMessage,
} from '@wix/error-handler-core/types';
import { isObject } from './isObject';

export function isHttpError(err: unknown): err is HttpError {
  return isObject<HttpError>(err) && err.response != null;
}

export function responseDataHasMessage(
  data: unknown,
): data is ErrorResponseMessage {
  return isObject<ErrorResponseMessage>(data) && data.message != null;
}

export function responseDataHasDetails(
  data: unknown,
): data is ErrorResponseDetails {
  return isObject<ErrorResponseDetails>(data) && data.details != null;
}
